section.cpnt-timeline .event {
  width: 350px;
  margin: 0 auto 30px;
}

section.cpnt-timeline .event > span {
  display: inline-block;
  vertical-align: top;
}

section.cpnt-timeline .event > span.col-1 {
  width: 25%;
  text-align: right;
  margin-right: 5%;
}

section.cpnt-timeline .event > span.col-2 {
  width: 65%;
}

section.cpnt-timeline .event > span.col-2 > div {
  margin-bottom: 20px;
}

section.cpnt-timeline .event > section {
  width: 300px;
  margin: 30px auto;
}

section.cpnt-timeline .divide {
  margin: 30px 0;
  position: relative;
}

section.cpnt-timeline .divide > span {
  display: inline-block;
}

section.cpnt-timeline .divide > span:first-child {
  width: 185px;
}

section.cpnt-timeline .divide > span:last-child {
  width: -webkit-calc(100% - 210px);
  border: 1px solid #777;
  position: absolute;
  right: 10px;
  top: 7px;
}

section.cpnt-timeline section.gallery > img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}