@import url(https://fonts.googleapis.com/earlyaccess/cwtexkai.css);
section.cpnt-main {
  position: fixed;
  width: 60%;
  height: 100vh;
  overflow: hidden;
}
section.cpnt-main-background {
  position: relative;
  height: 100vh;
  width: 100%;
}

section.cpnt-main-background > section.countdown {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  color: white;
  margin: 50% 10% 10%;
}

section.cpnt-main-background > section.theme {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

section.cpnt-main-background > section.theme.active {
  opacity: 1;
}

section.cpnt-main-background .content  {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 10% 20% 25% 10%;
  color: white;
}

section.cpnt-main-background .content.show-countdown > .bottom.countdown  {
  display: block;
}

section.cpnt-main-background .bottom  {
  position: absolute;
  top: 60%;
}

section.cpnt-main-background .title {
  font-size: 74px;
  line-height: 70px;
}

section.cpnt-main-background .desc {
  margin-top: 20px;
  font-size: 20px;
  font-family: cursive;
  line-height: 22px;
}
section.cpnt-countdown {
  font-size: 24px;
  margin: 25px;
  font-family: 'Open Sans Condensed', sans-serif;
  white-space: nowrap;
}

section.cpnt-countdown.vertical {
  font-family: 'Sacramento', cursive;
}


section.cpnt-countdown.vertical > span:first-child {
  text-align: right;
  margin-right: 20px;
  line-height: 47px;
  font-size: 35px;
}

section.cpnt-countdown.vertical > span {
  display: inline-block;
  width: 100px;
  line-height: 45px;
  font-size: 50px;
}
section.cpnt-sub > .cpnt-welcome {
  position: relative;
  height: 100vh;
  background: rgb(253, 181, 161);
}

section.cpnt-sub > .cpnt-welcome > .background {
  width: 100%;
  height: 100%;
  font-size: 30px;
  text-align: center;
  color: #444;
}

section.cpnt-welcome .content {
  position: absolute;
  margin: 10% 0 20%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
section.cpnt-welcome .content .bottom {
  position: absolute;
  top: 60%;
  width: 100%;
}
section.cpnt-welcome .city{
  font-size: 40px;
}

section.cpnt-welcome .date {
  font-size: 22px;
}

section.cpnt-welcome .location {
  margin: 30px 0 15px;
  font-size: 24px;
}
section.cpnt-timeline .event {
  width: 350px;
  margin: 0 auto 30px;
}

section.cpnt-timeline .event > span {
  display: inline-block;
  vertical-align: top;
}

section.cpnt-timeline .event > span.col-1 {
  width: 25%;
  text-align: right;
  margin-right: 5%;
}

section.cpnt-timeline .event > span.col-2 {
  width: 65%;
}

section.cpnt-timeline .event > span.col-2 > div {
  margin-bottom: 20px;
}

section.cpnt-timeline .event > section {
  width: 300px;
  margin: 30px auto;
}

section.cpnt-timeline .divide {
  margin: 30px 0;
  position: relative;
}

section.cpnt-timeline .divide > span {
  display: inline-block;
}

section.cpnt-timeline .divide > span:first-child {
  width: 185px;
}

section.cpnt-timeline .divide > span:last-child {
  width: -webkit-calc(100% - 210px);
  border: 1px solid #777;
  position: absolute;
  right: 10px;
  top: 7px;
}

section.cpnt-timeline section.gallery > img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

section.cpnt-schedule {
  padding-left: 0;
}

section.cpnt-schedule > .title {
  text-align: center;
  font-size: 40px;
  margin: 30px 0 40px;
}
section.cpnt-travel {
  padding-left: 0;
}

section.cpnt-travel > .title {
  text-align: center;
  font-size: 40px;
  margin: 30px 0 40px;
}
section.cpnt-sub {
  position: relative;
  width: 40%;
  height: 100%;
  margin-left: auto;

  color: rgba(255,255,255,.9);
  background: #4c4f52;
}

section.cpnt-sub > * {
  padding: 30px;
}

section.cpnt-home {
  min-height: 600px;
  font-size: 16px;
}

body,div {
  /* font-family: 'Sacramento', cursive; */
  font-family: 'Halant', cursive;
}

font.title-special {
  font-family: 'Sacramento', cursive;
}



@media screen and (max-width: 800px) {
  section.cpnt-main {
    width: 100%;
    position: static;
  }
  
  section.cpnt-sub {
    width: 100%;
  }
}
